.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
div{box-sizing: border-box;}
.black-nav{
  background:#000;
  width:100%;
  display: flex;
  color:#fff;
  padding-left:20px ;
}
.list{
  text-align: left;
  padding-left:20px;
  border-bottom:1px solid gray;
}
.modal{
  margin-top : 20px;
  padding : 20px;
  background : #eee;
  text-align : left;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
